.CategoriesBar{
    position: fixed;
    display: flex;
    width: 96.2%;
    margin: 0 1.5em;
    background-color: #0f0f0f;
    
    z-index: 998;
    overflow-y: hidden;
    list-style-type: none;
    -ms-overflow-style: none;
    scrollbar-width: none;
    scroll-behavior: smooth;

    .active {
        color:#000000;
        background-color: rgb(218, 218, 218);
        
        &:hover{
            color:#444444;
            background-color: rgb(218, 218, 218);
        }
    }

    li{
        margin: 1vh;
        overflow: visible;
        padding: 0.5rem ;
        border-radius: 5px;
        white-space: nowrap;
        color: whitesmoke;
        background-color: #444444; 
        cursor: pointer;
        
        &:hover{
            transition: 200ms;
            background-color: #565656; 
        }
    }

    .scrollX{
        position: fixed;
        top: 5em;
        height: 3em;
        color: white;
        border: none;
    }
    
    .catBarLeftBtn{
        left: 1.8em;        
        padding: 0em 5em 0em 1em;
        background: linear-gradient(-90deg, rgba(0, 0, 0, 0) 10%, #0f0f0f 60%);
    }
    
    .catBarRightBtn{
        right: 1.8em;
        padding: 0em 1em 0em 5em;
        background: linear-gradient(90deg, rgba(0, 0, 0, 0) 10%, #0f0f0f 60%);
    }
}

.CategoriesBar::-webkit-scrollbar {
    display: none;
}
