.navbar{
    width: 100%;
    position: fixed;
    display: flex;
    flex-direction: row;
    height: 4em;
    z-index: 999;
    background-color: #0f0f0f;
    margin: 0 0.5em;

    .startNavbar{
        flex: 0.2;
        display: flex;
        align-items: center;
        margin-left: 1em;
        
        // i{
            //     margin: 0 2em;
            // }
            
            .logoHomeLink{
                display: flex;
                align-items: center;
                justify-self: center;
                text-decoration: none;
                
                img{
                    height: 2em;
                }
                
                h3{
                    padding-bottom: 0.1em;
                    color: white;
                }
            }
        }
        
        .midNavbar{
            flex: 0.6;
            display: flex;
            align-items: center;
            justify-content: center;
            
            div{
                background-color: #121212;
                border: 1px solid #2c2c2c;
                border-radius: 30px;
                
                input{
                    width: 22em;
                    font-size: 1.2em;
                    padding: 0.3em 1em;
                    background: transparent;
                    border: none;
                    border-radius: 2em 0 0 2em;
                    color: white;
                    
                    &:focus{
                        outline: none;
                    }
                }
    
            i{
                background-color: #222222;
                padding: 0.6em 1em;
                border-radius: 0 2em 2em 0;
                cursor: pointer;
            }
        }
    }
    
    .endNavbar{
        flex: 0.2;
        display: flex;
        justify-content: end;
        align-items: center;
        margin-right: 1.8em;
        
        img{
            position: relative;
            left: 10px;
            border-radius: 50%;
            height: 2.5em;
            width: 2.5em;
        }
    }
}